* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  width: 500px;
  height: 400px;
  background-color: bisque;
  border-radius: 5px;
  margin: 20px auto;
  padding: 20px;
  line-height: 1.5em;
  text-align: center;
}

h1 {
  margin: 20px;
}

.input-box {
  margin: 2rem 0;
  width: 100%;
  height: 10%;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 5px;
  color: #495057;
  /* border-color: #28a745; */
}

@media (max-width: 600px) {
  .App {
    width: 90vw;
    height: 60vh;
  }
}